* {
  box-sizing: border-box;
  margin: 0;
}

body {
  margin: 0;
}

html {
  font-family: Roboto, Manrope, sans-serif;
  font-style: normal;
}

.product {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.product__img {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.product__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product__code {
  color: #616070;
  width: 100%;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.product__price {
  justify-content: space-between;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.product__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.product__price-values {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product__reviews {
  margin-bottom: 24px;
}

.product__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.product__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.reviews {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.reviews__text {
  text-align: right;
  white-space: nowrap;
  font-size: 10px;
  line-height: 14px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: flex;
}

.stars__star :last-child {
  margin-right: 0;
}

.stars__star :first-child {
  margin-left: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

